import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import Boss from "../components/Boss"
import Solider from "../components/Solider"
import ChildrenBoss1 from "../components/ChildrenBoss1";
import ChildrenBoss2 from "../components/ChildrenBoss2";
import AddBoss from "../components/AddBoss";
import EditBoss from "../components/EditBoss"

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect:'/Hello'
    },
    {
      path:'/Hello',
      component:HelloWorld
    },
    {
      path:'/Boss',
      component:Boss,
      children:[{
        path:"BossOne",component:ChildrenBoss1,children:[{
          path:"AddBoss",component:AddBoss},
          {path:"EditBoss/:id",component:EditBoss}]
      },
        {path:"BossTwo",component:ChildrenBoss2}]
    },
    {
      path:'/Solider',
      component:Solider
    }
  ]
})



