<template>
    <div>
      <table border="1">
        <tr>
          <th>ID</th>
          <th>姓名</th>
          <th>年龄</th>
          <th>江湖人称</th>
          <th>操作</th>
        </tr>

        <tbody>
        <tr v-for="list in Lists">
          <td>{{list.id}}</td>
          <td>{{list.username}}</td>
          <td>{{list.age}}</td>
          <td>{{list.content}}</td>
          <td>
              <router-link :to="'/Boss/BossOne/EditBoss/'+list.id" tag="button">修改</router-link>
            <button @click="deleteUser(list.id)">删除</button>
          </td>
        </tr>
        </tbody>
      </table>
<!--      <router-link to="/Boss/BossOne/AddBoss" tag="button">添加</router-link>-->
      <router-view/>
    </div>
</template>

<script>
    export default {
        name: "ChildrenBoss1",
        data(){
          return{
            Lists:[]
          }
        },
        methods:{
          findAll(){
            this.$http.get("http://localhost:8024/user/getusers").then((resp)=>{
              this.Lists = resp.data;
            })
          },
          deleteUser(id){
            this.$http.delete("http://localhost:8024/user/deleteuser?id="+id).then((resp)=>{
              console.log(resp.data);
              this.findAll();
            })
          }
        },
        component:{},
        created() {
              this.findAll();
          },
        watch: {
          $route: {
            handler: function(val, oldVal){
              if (val.path=="/Boss/BossOne") {
                this.findAll();
              }
            },
            // 深度观察监听
            deep: true
          }
        }
    }
</script>

<style scoped>

</style>



