<template>
    <div>
      <form>
        <div>
          <label for="userNameFor">姓名</label>
          <input type="text" id="userNameFor" v-model="user.username" placeholder="请输入姓名">
        </div>
        <div>
          <label for="ageFor">年龄</label>
          <input type="text"  id="ageFor" v-model="user.age" placeholder="请输入年龄">
        </div>
        <div>
          <label for="title">称号</label>
          <input type="text"  id="title" v-model="user.content" placeholder="请输入称号">
        </div>
        <button type="button" @click="updateUser">提交</button>
      </form>
    </div>
</template>

<script>
    export default {
        name: "UpdateBoss",
        data(){
          return{
            user:{}
          }
        },
        methods:{
          findById(id){
            this.$http.get("http://localhost:8024/user/getuser?id="+id).then((resp)=>{
              console.log(resp.data.userInfo);
              this.user = resp.data.userInfo;
            })
          },
          updateUser(){
            this.$http.post("http://localhost:8024/user/adduser",this.user).then((resp)=>{
              console.log(resp.data);
              this.$router.push("/Boss/BossOne");
            })
          }
        },
        created() {
          console.log(this.$route.params.id);
          this.user.id = this.$route.params.id;
          this.findById(this.user.id);
        }
    }
</script>

<style scoped>

</style>



